import React, { useEffect, useRef, useState } from "react";
import styled from "styled-components";
import { motion } from "framer-motion";

const useTypewriter = (text, delay = 0, speed = 50) => {
  const [displayedText, setDisplayedText] = useState("");
  const [isTyping, setIsTyping] = useState(true);

  useEffect(() => {
    const timeout = setTimeout(() => {
      let index = 0;
      const interval = setInterval(() => {
        setDisplayedText((prev) => prev + text.charAt(index));
        index++;
        if (index === text.length) {
          clearInterval(interval);
          setIsTyping(false); // 타이핑 완료 시 커서 멈춤
        }
      }, speed);
    }, delay);
    return () => clearTimeout(timeout);
  }, [text, delay, speed]);

  return { displayedText, isTyping }; // 객체로 반환
};

// 🌿 2. 스타일 컴포넌트
const IntroductionSection = styled.section`
  background-color: #464646;
  width: 100%;
  height: 500px; /* 모바일에서 높이 고정 X */
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 80px 20px;

  @media (max-width: 768px) {
  height: auto;       /* ✅ 모바일에서는 자동 높이 */
  }
`;


const ContentWrapper = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  width: 80%;
  max-width: 1200px;
  flex-direction: row;
  position: relative;

  @media (max-width: 768px) {
    flex-direction: column;
    text-align: center;
  }
`;


const ProfileImageWrapper = styled.div`
  position: relative;
  width: 280px;
  height: 280px;
  margin-right: 50px;

  @media (max-width: 768px) {
    margin-right: 0;
    margin-bottom: 20px;
  }
`;

const ProfileImage = styled.img`
  width: 100%;
  height: 100%;
  border-radius: 70%;
  object-fit: cover;
  transform: translateY(60px);
  z-index: 2;
  position: relative;
  
  @media (max-width: 768px) {
  margin-bottom: 50px;
  }
`;

const Sprout = styled(motion.img)`
  position: absolute;
  width: 28px;
  height: 28px;
  top: 82px;
  left: 22px;
  transform: rotate(-40deg);
  z-index: 1;
`;

const Sprout2 = styled(motion.img)`
  position: absolute;
  width: 24px;
  height: 24px;
  top: 66px;
  left: 46px;
  transform: rotate(-30deg);
  z-index: 1;
`;

const TextContainer = styled.div`
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
  max-width: 450px;

  @media (max-width: 768px) {
    text-align: center;
    align-items: center;
  }
`;

const SproutEmoji = styled.span`
  margin-left: 8px;
  display: inline-block;
`;


const Name = styled.h1`
  font-family: "Baloo Chettan 2", sans-serif;
  font-size: 96px;
  font-weight: 1500;
  color: #ffb625;
  margin-bottom: 10px;
  display: inline-block;
  text-align: center;

  @media (max-width: 768px) {
    margin-top: 50px;
    font-size: 36px;
  }
`;



const IntroText = styled.p`
  font-family: "Baloo Chettan 2", sans-serif;
  font-size: 24px;
  color: #c9c9c9;
  margin: 5px 0;
  white-space: normal;
  overflow: hidden;
  min-height: 96px;
  line-height: 1.3;

  @media (max-width: 768px) {
    font-size: 17px;
    line-height: 1.5;
  }
`;


const Cursor = styled.span`
  display: inline-block;
  width: 5px;
  height: 1em;
  background-color: #c9c9c9;
  margin-left: 6px;

  &.blinking {
    animation: blink 1s step-start infinite;
  }

  @keyframes blink {
    30% {
      opacity: 0;
    }
  }
`;


const Introduction = () => {
  const sectionRef = useRef();
  const [inView, setInView] = useState(false);

  const fullText =
    "   Hello! My name is Jane :)\n" +
    "   I'm a junior at University of Washington Bothell\n" +
    "   studying Computer Science and Software Engineering.";

  const { displayedText, isTyping } = useTypewriter(fullText, 200); // 구조 분해

  useEffect(() => {
    const observer = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting) setInView(true);
      },
      { threshold: 0.3 }
    );

    if (sectionRef.current) observer.observe(sectionRef.current);
    return () => {
      if (sectionRef.current) observer.unobserve(sectionRef.current);
    };
  }, []);

  return (
    <IntroductionSection ref={sectionRef}>
      <ContentWrapper>
        <ProfileImageWrapper>
          <ProfileImage src={`${process.env.PUBLIC_URL}/profile1.jpg`} alt="Profile" />
          <Sprout
            src={`${process.env.PUBLIC_URL}/logo.png`}
            alt="Sprout1"
            initial={{ y: 30, opacity: 0 }}
            animate={inView ? { y: 0, opacity: 1 } : {}}
            transition={{ type: "spring", stiffness: 120, delay: 0.2 }}
          />
          <Sprout2
            src={`${process.env.PUBLIC_URL}/logo.png`}
            alt="Sprout2"
            initial={{ y: 30, opacity: 0 }}
            animate={inView ? { y: 0, opacity: 1 } : {}}
            transition={{ type: "spring", stiffness: 120, delay: 0.4 }}
          />
        </ProfileImageWrapper>

        <TextContainer>
        <Name>
          JANE CHOI
        </Name>
          <IntroText>
            {displayedText}
            <Cursor className={isTyping ? "blinking" : ""} />
          </IntroText>
        </TextContainer>
      </ContentWrapper>
    </IntroductionSection>
  );
};

export default Introduction;