import React from "react";
import styled from "styled-components";

const FooterContainer = styled.footer`
  background-color: #3d3d3d;
  color: #c9c9c9;
  font-family: "Baloo Chettan 2", sans-serif;
  font-size: 24px;
  padding: 20px 60px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-width: 100%;
  box-sizing: border-box;
  position: relative;
  flex-wrap: wrap;
  height: auto;

  @media (max-width: 768px) {
    flex-direction: column;
    align-items: center;
    gap: 10px;
    padding: 20px 16px;
  }
`;


const LeftSection = styled.div`
  display: flex;
  align-items: center;
  gap: 10px;

  @media (max-width: 768px) {
    justify-content: center;
    width: 100%;
    flex-wrap: wrap;
  }
`;

const Logo = styled.img`
  height: 35px;
`;

const SocialIcons = styled.div`
  display: flex;
  gap: 15px;
  height: 25px;
  flex-wrap: wrap;

  @media (max-width: 768px) {
    justify-content: center;
    width: 100%;
  }
`;

const Icon = styled.img`
  height: 30px;
  cursor: pointer;
`;

const NameText = styled.span`
  font-size: 24px;
  white-space: nowrap;

  @media (max-width: 768px) {
    font-size: 16px;
  }
`;

const Footer = () => {
  return (
    <FooterContainer>
      <LeftSection>
        <Logo src={`${process.env.PUBLIC_URL}/logo.png`} alt="Logo" />
        <NameText>Jane Choi © 2025</NameText>
      </LeftSection>

      <SocialIcons>
          <a href="https://www.linkedin.com/in/jane026/" target="_blank" rel="noopener noreferrer">
            <Icon src={`${process.env.PUBLIC_URL}/linkedin.png`} alt="LinkedIn" />
          </a>
          <a href="https://www.instagram.com/wldnjs__26/" target="_blank" rel="noopener noreferrer">
            <Icon src={`${process.env.PUBLIC_URL}/instagram.png`} alt="Instagram" />
          </a>
          <a href="https://www.youtube.com/@this_is_ejan" target="_blank" rel="noopener noreferrer">
            <Icon src={`${process.env.PUBLIC_URL}/youtube.png`} alt="YouTube" />
          </a>
          <a href="https://github.com/jahabe" target="_blank" rel="noopener noreferrer">
            <Icon src={`${process.env.PUBLIC_URL}/github.png`} alt="GitHub" />
          </a>
      </SocialIcons>
    </FooterContainer>
  );
};

export default Footer;
